<template>
  <div class="EditChannel-container">
    <!--    ? my channels-->
    <van-cell title="我的频道">
      <template #right-icon>
        <van-button round size="mini" class="edit-btn" @click="isEnableEdit=!isEnableEdit">
          {{ !isEnableEdit ? '编辑' : '完成' }}
        </van-button>
      </template>
    </van-cell>
    <van-grid clickable>
      <van-grid-item :text="item.name"
                     v-for="(item,index) in userChannels"
                     @click="isEnableEdit?removeChannel(index,item):goThisChannel(index)"
                     class="animate__animated  animate__infinite "
                     :class="{animate__tada:isEnableEdit&&!fixedIndexArr.includes(index)}"
      >
        <template #icon>
          <van-icon v-show="isEnableEdit&&!fixedIndexArr.includes(index)" name="clear" color="#777"/>
        </template>
      </van-grid-item>
    </van-grid>
    <div class="m-t-30"></div>

    <!--    ? recommend channels-->

    <van-cell title="频道推荐">

    </van-cell>
    <van-grid clickable>
      <van-grid-item :text="'+ '+item.name" class=""
                     v-for="item in recommendChannels"
                     @click="addUserChannels(item)"
      />

    </van-grid>
  </div>
</template>

<script>
import getAllChannels from "../../../api/channels/getAllChannels";
import updateChannel from "../../../api/channels/updateChannel";
import deleteChannel from "../../../api/channels/deleteChannel";
import {mapState} from 'vuex';

export default {
  name: "EditChannel",
  components: {},
  props: ['userChannels'],
  data() {
    return {
      isEnableEdit: false,
      allChannels: [],
      //* 不要删除的数组标号
      fixedIndexArr:[0]
    }
  },
  methods: {
   async removeChannel(index, item) {
     if (this.fixedIndexArr.includes(index))return;
      //todo tip this maybe have bug , because cite index  not id


      if (this.userToken) {
        // * 如果用户已登录
        let {data}=await deleteChannel(item.id);
        this.$bus.$emit('deleteMyChannel', index);
      } else {

      }
    },
    goThisChannel(index) {
      this.$bus.$emit('getThisChannel', index);
    },
    async addUserChannels(item) {

      if (this.userToken) {
        // * 如果用户已登录
        try {
          let {data} = await updateChannel({
            channels: [{
              id: item.id,
              seq: this.userChannels.length+1
            }]
          });
          this.$bus.$emit('receiveUserChannel', item);
          console.log(data)

        } catch ({response: {data: {message}}}) {
          this.$toast(message);

        }

      } else {

      }
    },
    async loadAllChannels() {
      try {
        let {data: {data: {channels}}} = await getAllChannels();
        // console.log(channels);
        this.allChannels = channels
      } catch (e) {
        this.$toast('获取全部频道信息失败')
      }
    }
  },
  computed: {
    recommendChannels() {
      return this.allChannels.filter(item => this.userChannels.findIndex(e => e.id === item.id) === -1)
      // console.log(1)
      // this.allChannels.filter(item=>this.userChannels.findIndex(e=>e.id))
    },
    ...mapState(['userToken'])
  },
  watch: {},
  created() {
    // getAllChannels()
    this.loadAllChannels()
  },
  mounted() {
    // console.log(mapState(['userToken']))


    // console.log(this.allChannels);
    // console.log(this.userChannels);
  }
}
</script>

<style scoped lang="less">
.EditChannel-container {
  padding-top: 80px;
  box-sizing: border-box;

  .edit-btn {
    border: 1px solid #F85A5A;
    color: #F85A5A;
    padding: 0 20px;
  }

  /deep/ .van-cell__title {

    color: #333333;
    font-size: 32px;
  }

  .van-grid {
    //justify-content: space-around;
  }

  /deep/ .van-grid-item {

    .van-icon {
      //position: absolute;
      //right: 0;
      //top: 0;
      position: absolute;
      transform: translate(58px, -36px);
      font-size: 36px;
      //z-index: 1000;
    }

    flex-basis: unset !important;

    width: 160px;
    height: 86px;
    margin-top: 30px;
    margin-left: 23px;
    border-radius: 6px;

    .van-grid-item__content {
      &:after {
        border: unset;
      }

      //background: unset;
      //padding: 0;
      background: #F4F5F6;
      border-radius: 6px;

      span {
        font-size: 28px;
        color: #222222;


        text-overflow: ellipsis;
        white-space: nowrap;

      }
    }

  }
}
</style>
