<template>
  <div class="NewScrolls-container">
    <van-pull-refresh v-model="refreshing" @refresh="onRefresh">
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
        :error.sync="error"
        error-text="请求失败，点击重新加载"
      >
        <!--        <van-cell v-for="item in list" :key="item.art_id" :title="item.title"/>-->
        <router-link :to="{
          name:'article',
          params:{
            articleID:item.art_id,
          }
        }"
                     v-for="item in list"
                     :key="item.art_id"
        >
          <NewsItem :article="item" ></NewsItem>
        </router-link>
      </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
import getList from "../../../api/channels/getList";
import NewsItem from "../../../components/NewsItem";

export default {
  name: "NewScrolls",
  components: {NewsItem},
  props: ['newsInfo'],
  data() {
    return {
      requestParams: {
        channel_id: this.newsInfo.id,
        timestamp: Date.now()
      },
      list: [],
      loading: false,
      finished: false,
      refreshing: false,
      error: false,
    }
  },
  methods: {
    async onLoad() {
      try {
        let {data: {data}} = await getList(this.requestParams)
        if (this.refreshing) {// * 下拉更新
          this.refreshing = false;
          this.list = data.results
        } else {// * 上拉加载
          this.list.push(...data.results)
        }
        this.requestParams.timestamp = data.pre_timestamp
        // console.log(data);
        this.loading = false;
        // * 没有更多了
        if (!this.requestParams.timestamp) {
          this.finished = true;
        }


      } catch (e) {
        this.error = true;

      }


    },
    onRefresh() {
      // 清空列表数据
      this.finished = false;

      // 重新加载数据
      // 将 loading 设置为 true，表示处于加载状态
      this.loading = true;
      // this.list = [];
      this.requestParams.timestamp = Date.now();
      this.onLoad();
    },
  }
  ,
  computed: {}
  ,
  watch: {}
  ,
  created() {

  }
  ,
  mounted() {
  },
  beforeDestroy() {

  }
}
</script>

<style scoped lang="less">
/deep/ .van-list {
  padding-bottom: 100px;
}

.NewScrolls-container {
  height: calc(100vh - 100px - 92px - 84px);
  overflow-y: auto;
}
</style>
