<template>

  <div class="container">

    <van-nav-bar
      class="nav-bar"
    >
      <template #title>
        <van-button round class="search-btn" to="/search">
          <template #icon>
            <div class="f-r">
              <i class="iconfont icon-sousuo"></i>
              <span class="m-l-10">搜索</span>
            </div>
          </template>
        </van-button>
      </template>
    </van-nav-bar>
    <!--    ? hamburger btn-->
    <div class="hamburger-btn f-r" @click="popupShow=true">
      <i class="iconfont icon-gengduo"></i>
    </div>
    <!--    ? 导航tabs-->
    <div class="tabs-category-wrap">


      <van-tabs
        v-model="activeTab"
        animated
        swipeable
        title-active-color="#333333"
        title-inactive-color="#777777"
        border
        class="tabs-category"
      >
        <van-tab v-for="(item,index) in channels"
                 :title="item.name"
                 :key="item.id"
        >
          <NewScrolls
            :newsInfo="item"
          />

        </van-tab>
        <template #nav-right>
          <div class="occupy-space">

          </div>
        </template>
      </van-tabs>
    </div>
    <van-popup v-model="popupShow"
               closeable
               close-icon-position="top-left"
               round position="bottom"
               :style="{ height: '90%' }">
      <EditChannel :userChannels="channels"></EditChannel>
    </van-popup>

  </div>

</template>

<script>
import getChannels from "../../api/channels/getChannels";
import NewScrolls from "./component/NewScrolls";
import EditChannel from "./component/EditChannel";

export default {
  name: "Home",
  components: {NewScrolls, EditChannel},
  props: [],
  data() {
    return {
      channels: [],
      popupShow: false,
      activeTab: 0,

    }
  },
  methods: {
    async loadChannels() {
      try {
        let {data: {data: {channels}}} = await getChannels();
        this.channels = channels;
      } catch (e) {

      }

    }
  },
  computed: {},
  watch: {},
  created() {
    this.loadChannels();
    this.$bus.$on('receiveUserChannel', (channel) => {
      this.channels.push(channel);
    })
    this.$bus.$on('getThisChannel', (index) => {
      this.activeTab = index;
      this.popupShow = false;
      // console.log(index)
    })
    this.$bus.$on('deleteMyChannel', (index) => {
      this.channels.splice(index, 1);
    })

  },
  mounted() {

  },
  beforeDestroy() {
    this.$bus.$off(['receiveUserChannel', 'getThisChannel', 'deleteMyChannel'])
  }
}
</script>

<style scoped lang="less">
.container {
  /deep/ .van-nav-bar__title {
    max-width: unset;
  }

  /deep/ .van-nav-bar {
    position: sticky;
    top: 0;
  }

  /deep/ .van-tabs__wrap--scrollable {
    position: sticky;
    top: 92px;
    z-index: 1;
  }


  .search-btn {
    width: 555px;
    height: 64px;
    background: #5babfb;
    border: none;

    .van-button__icon {
      color: #fff;

      .iconfont {
        font-size: 38px;

      }

      span {
        font-size: 28px;
      }

    }


  }

  /deep/ .tabs-category {
    .van-tabs__wrap {
      height: 84px;
      border-bottom: 1px solid #edeff3;
    }

    .van-tabs__nav {
      padding: 0;
      //transform: scale(1);
    }

    .van-tab {
      width: 190px;
      font-size: 28px;
      border-right: 1px solid #edeff3;
    }

    .van-tabs__line {
      width: 31px;
      background-color: #3296fa;
      height: 8px;
      bottom: 8px;

    }

    .occupy-space {
      min-width: 84px;
      min-height: 84px;
      //background: #000;
    }


  }

  .tabs-category-wrap {
    position: relative;


  }

  .hamburger-btn {
    position: fixed;
    right: 0;
    top: 92px;
    z-index: 2;
    //left: 0;
    //top: 50%;
    width: 84px;
    height: 84px;
    //transform: translateY(-50%);
    //background: #000;
    background-color: rgba(255, 255, 255, 0.8);
    //border-left: 2px solid;
    //border-image: url("../../assets/img/gradient-gray-line.png");

    i {
      font-size: 33px;
    }

    &::before {
      position: absolute;
      left: 0;
      content: " ";
      display: block;
      background: url("../../assets/img/gradient-gray-line.png");
      background-size: contain;
      width: 2px;
      height: 80%;
    }
  }

}

</style>
